.footer {
    margin-top: 50px;
    border: 1px solid #000000;
    background-color: #292929;
    &_wrap {
        min-height: 255px;
        display: flex;
        justify-content: space-between;
        padding: 28px 10px 0;
        align-items: flex-start;
        flex-wrap: wrap;
        .sm-block({
            justify-content: space-around;
            padding-bottom: 15px;
        });
    }
    &_logo {
        .sm-block({
            width: 100%;
            text-align: center;
        });
        &--descr {
            color: #ababab;
            max-width: 170px;
            .sm-block({
                margin: 0 auto;
            });
        }
        &--vk {
            margin-top: 20px;
            display: inline-block;
            background: url(../img/ftvk.png) no-repeat;
            width: 42px;
            height: 42px;
        }
        &--inst {
            margin-top: 20px;
            margin-left: 10px;
            display: inline-block;
            background: url(../img/ftinst.png) no-repeat;
            width: 42px;
            height: 42px;
        }
    }
    &_catalog {
        .sm-block({
            margin-top: 35px;
        });
        padding: 0 10px;
        &--title {
            font-size: 16px;
            text-transform: uppercase;
            color: #fff;
            display: block;
            .sm-block({
                text-align: center;
            });
        }
        &--ul {
            .sm-block({
                margin-top: 20px;
            });
            margin-top: 30px;
            display: inline-block;
            &:last-child {
                margin-left: 40px;
            }
        }
        &--item {
            color: #ababab;
            display: block;
            position: relative;
            font-size: 13px;
            text-transform: uppercase;
            margin: 7px 0px;
            transition: .3s all;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_info {
        .sm-block({
           margin-top: 35px;
        });
        padding: 0 10px;
        &--title {
            font-size: 16px;
            text-transform: uppercase;
            color: #fff;
            display: block;
        }
        &--ul {
            .sm-block({
                margin-top: 20px;
            });
            margin-top: 30px;
            display: inline-block;
        }
        &--item {
            color: #ababab;
            display: block;
            position: relative;
            font-size: 13px;
            text-transform: uppercase;
            margin: 7px 0px;
            transition: .3s all;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &_inst {
        .md-block({
            display: none;
        });
        &--title {
            font-size: 16px;
            text-transform: uppercase;
            color: #fff;
            display: block;
        }
        &--ul {
            margin-top: 30px;
            display: inline-block;
        }
        &--item {
            background-color: #fff;
            width: 64px;
            height: 64px;
            display: block;
            float: left;
            margin: 4px;
        }
    }
}