@import "smart-grid.less";
@import "font.less";
@import "header.less";
@import "index.less";
@import "about.less";
@import "ship.less";
@import "size.less";
@import "contacts.less";
@import "catalog-inner.less";
@import "footer.less";


.reset();
.container {
    .container();
    position: relative;
}
html {
    font-size: 15px;
    .xs-block({
        font-size: 14px;
    });
    .xxs-block({
        font-size: 12px;
    });
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'Proxima Nova Rg';
    font-weight: normal;
    font-style: normal;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}
* {
    box-sizing: border-box;
}