.ship_content {
    margin-top: 55px;
}
.ship_wrap {
    .row-flex();
    justify-content: space-between;
    .sm-block({
        justify-content: center;
    });
}
.ship_left {
    .col();
    .size(5);
    .shift-left(1);
    .shift-sm-left(0);
    .size-sm(8);
    .size-xs(10);
}
.ship_right {
    .col();
    .size(5);
    .size-sm(8);
    .size-xs(10);
}
.ship__title {
    text-align: center;
    font-family: 'Moloko', sans-serif;
    color: #151515;
    font-size: 18px;
    line-height: 20px;
}
.ship__subtitle {
    text-transform: uppercase;
    font-weight: bold;
    margin: 45px 0 25px;
}
.ship__subtitle.left-two {
    margin-top: 20px;
}
.ship__subtitle.right-two {
    margin-top: 35px;
}
.ship__p {
    color: #151515;
    font-size: 15px;
    line-height: 22px;
    max-width: 380px;
}
.ship__p.decor {
    padding: 10px 0;
    border-bottom: 1px solid rgba(0,0,0,0.09);
    &:nth-child(6),
    &:nth-child(7) {
        border-bottom: none;
    }
}
.ship_form {
    margin-top: 80px;
    padding: 40px;
    background-color: #fbf9f9;
    &__title {
        color: #040404;
        font-size: 16px;
        text-align: center;
        margin-bottom: 30px;
    }
    &__label {
        display: block;
        color: #040404;
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 7px;
        margin-top: 15px;
    }
    &__input {
        width: 100%;
        display: block;
        border: 1px solid rgba(0,0,0,0.1);
        padding: 12px 15px;
        &:placeholder {
            color: #c9c4c4;
            font-size: 15px;
            line-height: 20px;
        }
    }
    &__textarea {
        display: block;
        width: 100%;
        padding: 12px 15px;
        border: 1px solid rgba(0,0,0,0.1);
        &:placeholder {
            color: #c9c4c4;
            font-size: 15px;
            line-height: 20px;
        }
    }
    &__btn {
        cursor: pointer;
        text-align: center;
        display: inline-block;
        margin: 15px auto 0;
        width: 100%;
        background-color: #000;
        color: #fff;
        font-size: 15px;
        font-weight: 700;
        padding: 14px 0;
        transition: .3s all;
        border: 2px solid #000;
        &:hover {
            color: #000;
            background-color: #fff;
            border: 2px solid #000;
        }
    }
}
