.catalog-inner {
    margin-top: 80px;
    &_wrap {
        .row-flex();
        justify-content: space-between;
    }
    &_sidebar {
        .col();
        .size(3);
        .sm-block({
            display: none;
        });
    }
    &_content {
        .col();
        .size(9);
        .size-sm(12);
        .row-flex();
        justify-content: flex-start;
        .md-block({
            justify-content: center;
        });
        align-items: flex-start;
    }
    &--card {
        .col();
        .size(4);
        .size-sm(6);
        .size-xs(10);
        position: relative;
        padding: 15px;
        padding-top: 25px;
        margin: 0;
        text-align: center;
        &:hover {
            box-shadow: -1px -1px 62px rgba(0, 0, 0, 0.06);
        }
    }
    &__title {
        font-size: 24px;
        font-weight: 700;
        line-height: 48px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: 0; left: 0;
            width: 53px;
            height: 2px;
            background-color: #313131;
        }
    }
    &__ul {
        margin-top: 5px;
    }
    &__li {
        padding: 15px 0;
        border-bottom: 1px solid #eee;
        &:first-child {
            padding-top: 22px;
        }
        a {
            font-size: 16px;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &__banner {
        margin-top: 40px;
    }
}
.catalog-inner__li.active {
    color: #ababab;
}