.fullwidth-bg {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    z-index: -100;
}

.fullwidth-bg__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.video-layer h1 {
    text-align:center;
    color:#fff;
    font-size: 70px;
    margin-top:17%;
}
.video-layer{
    background: rgba(0,0,0,0.25);
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: 5;
}
@media (max-width: 767px) {
    .fullwidth-bg {
        background: url(../img/intro.png) no-repeat;
        background-size: cover;
        background-position: center center;
    }

    .fullwidth-bg__video {
        display: none;
    }
}
.intro {
    height: 605px;
    text-align: center;
    color: #fff;
    &_title {
        padding-top: 10rem;
        font-family: 'Moloko', sans-serif;
        font-size: 5.3rem;
        font-weight: 400;
        line-height: 1.2;
    }
    &_descr {
        margin: 5px auto 0;
        max-width: 380px;
        font-size: 18px;
        line-height: 1.2;
    }
}
.scroll-down {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%);
    z-index: 10;
    text-align: center;
}
.arrow {
    background: url(../img/arrow.png) no-repeat;
    display: inline-block;
    width: 34px;
    height: 19px;
    position: relative;
    animation-name: drop;
    animation-duration: 1.5s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-play-state: running;
}
@keyframes drop {
    0% {
        top: 0;
        opacity: 0;
    }
    50% {
        top: 15px;
        opacity: 1;
    }
    100% {
        top: 30px;
        opacity: 0;
    }
}
.catalog {
    &_item {
        margin: 35px auto 0;
        &--wrap {
            .row-flex();
            .md-block({
                justify-content: center;
            });
            align-items: flex-start;
        }
        &--card {
            .col();
            .size(3);
            .size-lg(4);
            .size-md(5);
            .size-sm(6);
            .size-xs(10);
            position: relative;
            padding: 15px;
            padding-top: 25px;
            margin: 0;
            text-align: center;
            &:hover {
                box-shadow: -1px -1px 62px rgba(0, 0, 0, 0.06);
            }
        }
        &--img {
            max-height: 120px;
        }
        &--title {
            margin: 20px auto 12px;
            max-width: 220px;
            text-transform: uppercase;
        }
        &--bottom {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &--price {
            s {
                color: #b9b8b8;
                font-size: 14px;
            }
            span {
                padding: 0px 10px;
                border-radius: 5px;
                background-color: #00ff00;
                font-size: 14px;
                line-height: 20px;
            }
        }
        &--basket {
            display: flex;
            align-items: center;
            font-size: 14px;
            padding: 10px 25px;
            padding-right: 42px;
            border: 1px solid #000;
            border-radius: 20px;
            transition: .3s all;
            position: relative;
            &:hover {
                background-color: #e86064;
                border: 1px solid #e86064;
                color: #fff;
            }
            &:hover::after {
                background: url(../img/shopping-cart-empty-side-view-2.svg) no-repeat;
                background-size: cover;
                background-position: center center;
                width: 15px;
                height: 15px;
            }
            &::after {
                content: '';
                position: absolute;
                top: 10px; right: 20px;
                background: url(../img/shopping-cart-empty-side-view.svg) no-repeat;
                background-size: cover;
                background-position: center center;
                width: 15px;
                height: 15px;
            }
        }
        &--hover {
            opacity: 0;
            border-top: 1px solid #191919;
            margin: 15px auto 20px;
            padding-top: 15px;
            font-size: 14px;
            max-width: 240px;
            transition: .3s all;
            p {
                text-align: left;
            }
            .md-block({
                opacity: 1;
            });
        }
    }
}
.catalog-inner--card:hover .catalog_item--hover{
    opacity: 1;
}
.hit {
    position: absolute;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    padding: 5px 12px;
    border-radius: 4px;
    background-color: #fb6b6b;
    color: #fff;
    top: -20px;
    left: 10px;

}
.newcat {
    margin-top: 55px;
    text-align: center;
    &_title {
        .semi();
        font-size: 21px;
    }
    &_descr {
        display: inline-block;
        margin-top: 15px;
        font-style: italic;
        color: #090909;
        font-weight: 300;
    }
    &_content {
        margin-top: 80px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        .md-block({
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        });
        .md-block({
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        });
        .mob-block({
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        });
    }
    &_item {
        position: relative;
        overflow: hidden;
        transition: .3s all;
        opacity: 1;
        &:hover {
            opacity: .35;
            background-blend-mode: #000;
        }
        &--title {
            position: absolute;
            top: 50%; left: 12px;
            transform: translate(0, -50%);
            color: #090909;
            font-size: 18px;
            font-weight: bold;
            text-align: left;
            .xxs-block({
                left: 5px;
            });
            span {
                font-weight: normal;
            }
        }
            &:first-child {
                grid-row-start: span 2;
                grid-column: span 4;
                background: url(../img/1-min.png) no-repeat;
                background-size: cover;
                background-position: center center;
                margin: 0 7.5px;
                .md-block({
                    grid-row-start: span 2;
                    grid-column: 1;
                    margin-bottom: 15px;
                });
                .mob-block({
                    grid-row-start: span 2;
                    grid-column: 1;
                    margin-bottom: 15px;
                });
                .newcat_item--title {
                    top: 85px;
                    right: 32px;
                    left: auto;
                    transform: none;
                }
            }
            &:not(:first-child) {
                margin: 0 7.5px 15px;
                grid-column: span 4;
                .md-block({
                    grid-column: span 1;
                });
                .mob-block({
                    grid-column: span 1;
                    grid-row: span 1;
                });
                
            }
        &:nth-child(2) {
            background: url(../img/2-min.png) no-repeat;
            background-size: cover;
            background-position: center center;
            height: 227px;
        }
        &:nth-child(3) {
            background: url(../img/3-min.png) no-repeat;
            background-size: cover;
            background-position: center center;
        }
        &:nth-child(4) {
            background: url(../img/4-min.png) no-repeat;
            background-size: cover;
            background-position: center center;
            .md-block({
                grid-column: span 2;
            });
        }
            &:nth-child(5) {
                margin: 0 7.5px;
                grid-column: span 5;
                background: url(../img/6-min.png) no-repeat;
                background-size: cover;
                background-position: center center;
                .md-block({
                    grid-column: span 1;
                });
                .newcat_item--title {
                    top: 60px;
                    transform: none;
                }
            }
            &:last-child {
                margin: 0 7.5px;
                grid-column: span 7;
                background: url(../img/5-min.png) no-repeat;
                background-size: cover;
                background-position: center center;
                .md-block({
                    grid-column: span 1;
                });
                .mob-block({
                    margin-top: 15px;
                });
            }
    }
}

.why {
    margin-top: 110px;
    text-align: center;
    &_title {
        .semi();
        font-size: 21px;
        padding: 0 10px;
    }
    &_descr {
        display: inline-block;
        margin-top: 5px;
        font-style: italic;
        color: #090909;
        font-weight: 300;
    }
    &_wrap {
        margin-top: 70px;
        .row-flex();
        justify-content: center;
    }
    &_item {
        .col();
        .size(2.8);
        .size-md(5);
        .size-sm(5.5);
        .size-xs(10);
        margin: 16px;
        border: 1px solid rgba(0,0,0,0.19);
        padding: 0px 30px;
        &--img {
            margin: 30px auto 25px;
            width: 70px;
            height: 70px;
        }
        &--title {
            .semi();
            color: #090909;
            font-size: 18px;
        }
        &--text {
            display: inline-block;
            margin: 25px auto 60px;
            line-height: 1.5;
        }
    }
}
.text {
    margin-top: 75px;
    padding: 0 30px;
    &_title {
        .semi();
        font-size: 18px;
        margin-bottom: 20px;
    }
    &_p {
        line-height: 1.5;
        &:last-child {
            padding-top: 25px;
        }
    }
}


.overlay {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
}

.popup {
    position: fixed;
    width: 685px;
    box-shadow: 0 0 95px rgba(0, 0, 0, 0.06);
    background-color: #fff;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    padding-right: 57px;
    .sm-block({
        width: 100%;
        height: 100vh;
        font-size: 15px;
        padding: 20px;
    });
    &_wrap {
        display: flex;
        .sm-block({
            height: 100vh;
            justify-content: center;
            align-items: center;
        });
    }
    &_img {
        margin-top: 20px;
        .sm-block({
            display: none;
        });
    }
    &_form {
        text-align: center;
        &--title {
            font-size: 18px;
            .semi();
        }
        &--descr {
            display: inline-block;
            margin: 10px auto 15px;
        }
        &--input {
            margin: 10px 0;
            padding: 16px;
            border: 1px solid rgba(0,0,0,0.17);
            display: block;
            width: 297px;
            &::placeholder {
                color: #161616;
                line-height: 15px;
                font-size: 15px;
            }
        }
        &--btn {
            cursor: pointer;
            display: block;
            width: 297px;
            background-color: #fa5b66;
            line-height: 16px;
            padding: 16px 0;
            color: #fff;
            font-size: 16px;
            font-weight: 700;
            position: relative;
            &::after {
                content: '';
                position: absolute;
                top: 50%; right: 16%;
                transform: translate(-50%,-50%);
                background: url(../img/btn-arrow.png) no-repeat;
                width: 15px;
                height: 12px;
                transition: .3s all;
            }
            &:hover::after {
                right: 12%;
            }
        }
    }
}

.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 23px;
    height: 23px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #000;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #000;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}
.success {
    display: none;
    border: 1px solid green;
    padding: 10px 15px;
    margin-top: 10px;
}
