.arrow {
  background: url(../img/arrow.png) no-repeat;
  display: inline-block;
  width: 34px;
  height: 19px;
  position: relative;
  animation-name: drop;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-play-state: running;
}

@keyframes drop {
  0% {
    top: 0;
    opacity: 0;
  }

  50% {
    top: 15px;
    opacity: 1;
  }

  100% {
    top: 30px;
    opacity: 0;
  }
}

