.newcat {
  margin-top: 55px;
  text-align: center;
}

.newcat_title {
  font-family: 'Proxima Nova Lt';
  font-weight: 600;
  font-style: normal;
  font-size: 21px;
}

.newcat_descr {
  display: inline-block;
  margin-top: 15px;
  font-style: italic;
  color: #090909;
  font-weight: 300;
}

.newcat_content {
  margin-top: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.newcat_item {
  position: relative;
  overflow: hidden;
  transition: .3s all;
  opacity: 1;
}

.newcat_item:hover {
  opacity: .35;
  background-blend-mode: #000;
}

.newcat_item--title {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translate(0, -50%);
  color: #090909;
  font-size: 18px;
  font-weight: bold;
  text-align: left;
}

.newcat_item--title span {
  font-weight: normal;
}

.newcat_item:first-child {
  grid-row-start: span 2;
  grid-column: span 4;
  background: url(../img/1-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
  margin: 0 7.5px;
}

.newcat_item:first-child .newcat_item--title {
  top: 85px;
  right: 32px;
  left: auto;
  transform: none;
}

.newcat_item:not(:first-child) {
  margin: 0 7.5px 15px;
  grid-column: span 4;
}

.newcat_item:nth-child(2) {
  background: url(../img/2-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
  height: 227px;
}

.newcat_item:nth-child(3) {
  background: url(../img/3-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
}

.newcat_item:nth-child(4) {
  background: url(../img/4-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
}

.newcat_item:nth-child(5) {
  margin: 0 7.5px;
  grid-column: span 5;
  background: url(../img/6-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
}

.newcat_item:nth-child(5) .newcat_item--title {
  top: 60px;
  transform: none;
}

.newcat_item:last-child {
  margin: 0 7.5px;
  grid-column: span 7;
  background: url(../img/5-min.png) no-repeat;
  background-size: cover;
  background-position: center center;
}

