  line-height: 16px;
  padding: 16px 0;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  position: relative;
}

.popup_form--btn::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 16%;
  transform: translate(-50%, -50%);
  background: url(../img/btn-arrow.png) no-repeat;
  width: 15px;
  height: 12px;
  transition: .3s all;
}

.popup_form--btn:hover::after {
