
.about_intro {
    margin-top: 35px;
    background: url(../img/1about.png) no-repeat;
    background-size: cover;
    background-position: center center;
    &--text {
        display: flex;
        min-height: 357px;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        color: #f4eaea;
    }
    &--title {
        font-size: 36px;
    }
    &--descr {
        font-style: italic;
        font-size: 18px;
        margin-top: 10px;
    }
}
.about_content {
    margin-top: 80px;
    color: #080808;
    &--wrap {
        .row-flex();
        justify-content: space-between;
        align-items: center;
        .md-block({
            justify-content: center;
        });
    }
    &--text {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
    }
    &--img {
        .col();
        .size(5);
        .size-md(8);
        .size-sm(10);
        .md-block({
            margin-top: 35px;
        });
    }
    &--title {
        font-family: "Moloko", sans-serif;
        font-size: 24px;
    }
    &--p {
        padding-top: 35px;
        line-height: 1.5;
    }
    &--assort {
        margin-top: 60px;
    }
    &--ul {
        margin-top: 35px;
        display: inline-block;
        &:last-child {
            margin-left: 70px;
        }
    }
    &--li {
        list-style: disc;
        margin-left: 20px;
        padding: 2px 0;
    }
    &--bottom {
        margin: 50px auto 80px;
        .col();
        .size-sm(10);
    }
}