.cont {
    margin: 55px auto 140px;
    .xs-block({
        margin: 55px auto 0;
    });
    &_wrap {
        .row-flex();
        justify-content: center;
        align-items: center;
    }
    &_item {
        .col();
        .size(3);
        .size-md(6);
        .size-xs(10);
        .size-xxs(12);
        text-align: center;
        .md-block({
            margin: 15px 0;
        });
        &--title {
            .semi();
            font-size: 18px;
            margin: 30px auto 20px;
        }
        &--mail {
            display: inline-block;
            margin-top: 20px;
            .semi();
        }
    }
    &_form {
        margin-top: 120px;
        text-align: center;
        .xs-block({
            margin-top: 80px;
        });
        &__title {
            .semi();
            font-size: 18px;
            margin-bottom: 30px;
        }
        &__input,
        &__textarea {
            width: 100%;
            display: block;
            border-bottom: 1px solid rgba(0,0,0,0.17);
            padding: 12px 15px;
            margin: 5px 0;
            &::placeholder {
                color: #000;
                font-size: 16px;
                line-height: 16px;
            }
        }
        &__textarea {
            border: none;
            border-bottom: 1px solid rgba(0,0,0,0.17);
        }
        &__btn {
            cursor: pointer;
            float: right;
            margin-top: 35px;
            background-color: #313131;
            color: #fff;
            font-size: 16px;
            padding: 16px 0;
            padding-left: 66px;
            padding-right: 104px;
            transition: .3s all;
            position: relative;
            .xs-block({
                float: none;
                display: inline-block;
            });
            &::after {
                content: '';
                position: absolute;
                top: 50%; right: 28%;
                transform: translate(-50%,-50%);
                background: url(../img/btn-arrow.png) no-repeat;
                width: 15px;
                height: 12px;
                transition: .3s all;
            }
            &:hover::after {
                right: 24%;
            }
        }
    }
}