.size_content {
    margin-top: 55px;
    .color {
        color: #f45f42;
    }
}
.size__subtitle {
    text-transform: uppercase;
    font-weight: bold;
    margin: 45px 0 25px;
}
.size__p {
    .col();
    .size(11);
    color: #151515;
    line-height: 22px;
    padding: 2px 0;
}

.size_table {
    .col();
    .size(12);
    .size-md(11);
    .size-xs(12);
    display: grid;
    text-align: center;
    grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
    .bg {
        background: #eee;
    }
    .first {
        padding-left: 10px;
    }
    .last {
        font-family: 'Proxima Nova Rg';
        font-weight: normal;
        font-style: normal;
        text-align: left;
        margin: auto 0;
        padding-right: 5px;
    }
    .last-title {
        text-align: left;
        margin: auto 0;
    }
}
.size_table2 {
    grid-template-columns: 3fr 1fr 1fr 2fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr; 
    .last {
        padding-left: 5px;
    }
}
.size_td {
    padding: 10px 0px;
    display: flex;
    align-items: center;
    margin: auto;
    width: 100%;
    height: 100%;
    p {
        color: #151515;
        .xs-block({
            font-size: 12px;
        });
    }
} 
